import { LitElement, html, css } from 'lit';
import CONFIG from '../globals/config';

class RestaurantDetail extends LitElement {
  static properties = {
    id: { type: String },
    pictureId: { type: String },
    name: { type: String },
    rating: { type: Number },
    city: { type: String },
    address: { type: String },
    description: { type: String },
    categories: { type: Object },
  };

  static styles = css`
    * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
    }
    
    .restaurant__poster {
      width: 100%;
      max-width: 800px;
      border-radius: 12px;
    }
    
    .restaurant__poster, .restaurant__info h4, .restaurant__overview h3{
      margin: 12px 0;
    }

    .restaurant__overview > p {
      text-align: justify;
      line-height: 1.5rem;
    }

    @media screen and (max-width: 499px) {
      .restaurant__title {
        text-align: center;
        color: #FD5E53;
      }
    
      .restaurant__info, .restaurant__overview {
        text-align: left;
        margin: 12px 0;
      }

      .restaurant__overview > p {
        text-align: justify;
      }
    }

    @media screen and (min-width: 650px) {
      .restaurant__title {
        grid-column-start: 1;
        grid-column-end: 3;
        color: #FD5E53;
      }
    
      .restaurant__overview {
        grid-column-start: 1;
        grid-column-end: 3;
      }
      
      .restaurant__overview > p {
        padding: 0 3em;
      }
  `;

  render() {
    return html`
      <h2 class="restaurant__title">${this.properties.name}</h2>
      <img class="restaurant__poster" src="${CONFIG.BASE_IMAGE_URL_MEDIUM + this.properties.pictureId}" alt="${this.properties.name}">
      <div class="restaurant__info">
        <h3>Information</h3>
        <h4>Address</h4>
        <p>${this.properties.address}, ${this.properties.city}</p>
        <h4>Rating</h4>
        <p>&#11088 ${this.properties.rating}/Reviews</p>
        <h4>Categories</h4>
        <p>${this.properties.categories}</p>
      </div>
      <div class="restaurant__overview">
        <h3>Overview</h3>
        <p>${this.properties.description}</p>
      </div>
    `;
  }
}

customElements.define('restaurant-detail', RestaurantDetail);
